.movie {
  position: relative;
  width: 25rem;
  margin: 1rem;
  background-color: #101010;
  height: 100%;

  &__image {
    overflow: hidden;
    height: 35rem;

    img {
      width: 100%;
      overflow: hidden;
      height: 35rem;
      object-fit: cover;
    }
  }

  &__caption {
    padding: 1rem 1rem 0 1rem;
    margin-bottom: -1rem;
    word-wrap: normal;
    white-space: normal;
    line-height: 1.5;
  }

  &__title {
    font-size: 1.5rem;
  }

  &__rating {
    background-color: black;
    width: 4.5rem;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    background-color: darkgreen;
    font-weight: bold;
  }

  &__overview {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0%;
    width: 100%;
    overflow: hidden;
    word-wrap: normal;
    white-space: normal;
    font-size: 1.5rem;
    line-height: 1.5;
    padding: 1rem 1rem;
    background-color: rgba($color: #000000, $alpha: 0.85);
    transition: 0.3s;
    overflow-y: auto;
    opacity: 0;
  }
}

.movie:hover > .movie__overview {
  height: 100%;
  opacity: 1;
}